@import url(https://fonts.googleapis.com/css?family=Raleway:100,300&display=swap);
body {
  text-align: center;
  color: #535353;
  margin: 0;
  font-family: 'Raleway', sans-serif;
  font-weight: 300; }

h1, h2, h3, h4, h5, h6, p, a {
  padding: 0;
  margin: 0;
  font-weight: 300; }

a {
  color: #535353;
  -webkit-transition: 0.5s color;
  transition: 0.5s color; }
  a:hover {
    color: #f96167; }

button {
  background: none;
  border: none;
  outline: none;
  color: #535353;
  font-size: 16px;
  font-family: 'Raleway', sans-serif; }

ul {
  padding: 0; }

ul li {
  display: inline-block;
  list-style: none;
  margin-right: 5px;
  padding: 0; }

a, p, button {
  text-decoration: none; }

.fa {
  font-size: 16px;
  -webkit-transition: 0.5s color;
  transition: 0.5s color; }
  .fa:hover {
    color: #b1393d; }

.highlight {
  color: #f96167; }

.container {
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px; }

.profile {
  padding-bottom: 1rem; }
  .profile ul {
    margin: 0.5rem 0 0 0;
    padding: 0;
    align-self: flex-end; }

.projects-nav ul li {
  cursor: pointer;
  padding: 3px 0; }
  .projects-nav ul li button {
    cursor: pointer;
    padding: 0;
    -webkit-transition: 0.5s color;
    transition: 0.5s color; }
    .projects-nav ul li button:hover {
      color: #f96167; }

.project {
  min-height: 548px; }
  .project .project-name.hide {
    display: none; }
  .project .project-detail {
    position: relative; }
  .project .project-desc {
    line-height: 1.6;
    padding-bottom: 2rem; }
  .project img {
    width: auto;
    height: 250px;
    margin-bottom: 1rem; }
  .project .skill span {
    font-size: 13px; }
  .project .skill-item {
    font-size: 13px;
    display: inline; }
  .project .button-group a {
    margin-right: 20px; }
  .project .button-group .demo {
    padding: 5px 20px;
    background: #f96167;
    color: white;
    -webkit-transition: .5s background;
    transition: .5s background; }
    .project .button-group .demo:hover {
      background: #b1393d; }
  .project .bio {
    text-align: left;
    text-align: left;
    padding: 37px 0px; }
    .project .bio .bio-skills {
      margin: 2rem 0; }
      .project .bio .bio-skills li {
        display: block; }
    .project .bio p {
      line-height: 1.6;
      margin-bottom: 1rem; }

footer img {
  height: 20px;
  width: auto;
  margin-right: 5px; }

.bio-inner img {
  display: block;
  margin: 0 auto;
  margin-bottom: 30px; }

@media only screen and (min-width: 800px) {
  body {
    text-align: left;
    text-align: initial; }
  .container {
    padding: 0 55px;
    height: 100vh; }
  .project {
    min-height: 430px; }
    .project .bio {
      padding: 37px 0; }
  .profile .profile-info {
    display: flex;
    justify-content: space-between;
    flex-grow: 1; }
  .project .project-name {
    display: flex;
    align-items: center; }
  .project .project-detail {
    margin-left: 2rem;
    padding: 30px; }
  .projects-nav ul {
    display: flex; }
  .bio-inner {
    display: flex; }
    .bio-inner img {
      margin-right: 30px; } }

